@import '../../vars';

.solutions {
  @media screen and (min-width: 768px) {
    display: flex;
    height: 589px;
  }
  @media screen and (min-width: 1360px) {
    height: 460px;
  }
}

.solutions-pic {
  background-image: image-set(
                  url("../../images/images/home/people@2x.webp") 2x,
                  url("../../images/images/home/people.webp") 1x,
                  url("../../images/images/home/people@2x.jpg") 2x,
                  url("../../images/images/home/people.jpg") 1x
  );
  max-width: 100%;
  height: 220px;
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
    width: calc(100% - 400px);
    height: 100%;
  }
  @media screen and (min-width: 1360px) {
    width: calc(100% - 690px);
  }
}

.solution-inner {
  background-color: $accent-green;
  @media screen and (min-width: 768px) {
    width: 400px;
  }
  @media screen and (min-width: 1360px) {
    width: 690px;
  }
}

.solutionContainer {
  padding-top: 79px;
  padding-bottom: 79px;
  @media screen and (min-width: 768px) {
    padding-top: 62px;
    padding-bottom: 62px;
  }
  @media screen and (min-width: 1360px) {
    padding-top: 80px;
    padding-bottom: 86px;
  }
}

.solutions-above-title {
  color: $text-light;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.375;
  margin-bottom: 16px;
  @media screen and (min-width: 768px) {
    font-size: 18px;
  }
}

.solutions-title {
  color: $text-light;
  font-size: 32px;
  font-weight: 400;
  line-height: 1.375;
  text-align: left;
  margin-bottom: 24px;
  @media screen and (min-width: 768px) {
    font-size: 40px;
    line-height: 1.2;
  }
}

.solutions-sub-title {
  color: $text-light;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.375;
  text-align: left;
  padding-bottom: 24px;
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.375;
  }
}

.solution-btn {
  width: 146px;
  border: 1px solid $text-light;
  background-color: $accent-green;
}